import { BufferData } from './blobHash.worker';
import BlobHash from './blobHash.worker?worker';

type BufferHashData = BufferData & {
  hash: string;
}

export type BlobHashResult = {
  blob: Blob;
  hash: string;
}

/**
 * 删除重复项
 * @param list 列表
 */
export function deduplicateList<T extends object, K extends keyof T>(list: T[], key: K) {
  const seen = new Set<T[K]>();
  return list.filter(item => {
    const isNew = !seen.has(item[key]);
    isNew && seen.add(item[key]);
    return isNew;
  });
}

/**
 * 按照给定的顺序，将 a b 两个列表合并
 * 这里不进行排重
 * @param aList a 列表
 * @param bList b 列表
 * @param key key
 * @param order 顺序数组
 */
export function sortListByKey<T extends object, U extends keyof T>(aList: T[], bList: T[], key: U, order: T[U][]) {
  const aMap = new Map(aList.map(item => [item[key], item]));
  const bMap = new Map(bList.map(item => [item[key], item]));
  const result: T[] = [];
  order.forEach(key => {
    if (aMap.has(key)) {
      result.push(aMap.get(key)!);
    }
    if (bMap.has(key)) {
      result.push(bMap.get(key)!);
    }
  });
  return result;
}

/**
 * 计算文件哈希值
 * @param blob blob 文件
 */
export function calcBlobHash(blob: Blob) {
  const blobHash = new BlobHash();
  blob.arrayBuffer().then(buffer => {
    blobHash.postMessage({
      buffer,
      type: blob.type
    }, [buffer]);
  });
  return new Promise<BlobHashResult>((resolve, reject) => {
    blobHash.onmessage = (event: MessageEvent<BufferHashData>) => {
      const { buffer, type, hash } = event.data;
      const blob = new Blob([buffer], { type });
      resolve({ blob, hash });
    };
    blobHash.onerror = (event: ErrorEvent) => {
      reject(event.error);
    };
  }).finally(() => {
    blobHash.terminate();
  });
}

/**
 * 比较两个文件内容是否一致
 * @param a 文件 a
 * @param b 文件 b
 */
export function compareBlobs(a: Blob, b: Blob) {
  return Promise.all([calcBlobHash(a), calcBlobHash(b)]).then(([aResult, bResult]) => {
    return aResult.hash === bResult.hash;
  });
}
