import Dexie, { type EntityTable } from 'dexie';
import { SettingCatogory, SettingControl } from '@/pages/settings/utils';

export type SettingKey =
'initialized' |
'isbnDataUrl' |
'language' |
'darkMode' |
'author' |
'contactMe' |
'version' |
'gratitude';

export type Setting = {
  /** 顺序索引 */
  index: number;
  /** 创建时间 */
  createDate: number;
  /** 更新时间 */
  updateDate: number;
  /** 设置项标记 */
  key: SettingKey;
  /** 设置值 */
  value: string | boolean | number;
  /** 设置名称 */
  label: string;
  /** 是否只读 */
  readonly: boolean;
  /** 是否隐藏 */
  hidden: boolean;
  /** 设置项分类 */
  catogory: SettingCatogory;
  /** 设置项控制类型 */
  control?: SettingControl
}

export const db = new Dexie('settings') as Dexie & {
  settings: EntityTable<Setting, 'key'>;
};

db.version(1).stores({
  settings: '&key, label, catogory, index',
});
